.warning {
  padding: 12svh 8vw;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
  border: 1px solid black;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.warning h2 {
  max-width: 300px;
}
.warningButtons {
  display: flex;
  gap: 30px;
  margin-top: 20px;
}
.warningButtons button {
  padding: 10px 30px;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
