.headCase,
.headCaseMoreThree {
  margin: 0 auto;
  font-size: 26px;
  letter-spacing: 1px;
  font-weight: 100;
  margin-bottom: 15px;
}
.headCaseMoreThree {
  align-self: center;
  font-size: 36px;
}
.vizualContainer {
  min-height: 50vh;
  padding: 40px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: max-content;
}
.vizualContainerMoreThree {
  padding: 20px 30px;
  border-radius: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 5vw;
  grid-row-gap: 2vh;
  width: max-content;
}
.variants_and_result_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3vw;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.current_results {
  color: black;
  background: #40404085;
  height: max-content;
  text-align: center;
  transition: 1s;
  border-radius: 16px;
  width: 30vw;
  overflow: hidden;
  font-size: 44px;
  position: relative;
  margin: 3px 0;
}
.current_results div {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.current_results span {
  position: relative;
  z-index: 1;
}
.resultVariant {
  max-width: 60vw;
}
