.main {
  background-image: url("../../../Images/fone.png");
  background-repeat: no-repeat;
  background-position: center;
  min-height: 85vh;
}
.mainPDFContainer {
  padding: 10px 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}

.pdfParamName,
.pdfParamValue {
  color: white;
  font-weight: 100;
}
.pdfParamValue {
  color: aqua;
}
.children {
  max-width: 375px;
  padding: 5px 10px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid white;

}
